


























import request from '../../utils/req.js'
import cfg from '../../cfg.js'
import all from '../../common/global.js'
import user from '../../user/basic.js'


export default {
	data() {
		return {
			coupons: [], //套餐    
			currentSelected: -1,
			isUseCoupon: false, //是否使用套餐充值
			balanceTmp: {},
			length: 0,
			targetUrl: ''
		}
	},
	onLoad: function(option) {

		request.req({

			url: cfg.qjUrl + 'charge/list',
			method: 'GET',
			data: {},
			success: (res) => {
				console.log("djj", res.data)
				for (let i = 0; i < res.data.c.length; i++) {
					this.coupons.push(res.data.c[i])
				}

				this.coupons = this.coupons,
					this.length = --res.data.c.length

				if (option.targetUrl) {

					this.targetUrl = option.targetUrl

				}
			},
			fail: function(res) {
				console.log('网络访问失败！')
			}
		})

	},
	methods: {
		selectCoupon: function(e) {
			
			console.log("djj", e)
			var that = this;
			if (this.currentSelected == e.currentTarget.dataset.id) {	
				this.currentSelected = -1,
				this.isUseCoupon = false
			} else {
				this.currentSelected = e.currentTarget.dataset.id,
				this.isUseCoupon = true

			}
		},
		submit: function(e) {
			console.log(e);
			var balanceTmp = new Object();
			balanceTmp.isUseCoupon = this.isUseCoupon
			if (balanceTmp.isUseCoupon) {
				console.log("coupons", this.coupons)
				balanceTmp.c_id = this.coupons[this.currentSelected].id
				balanceTmp.amount_payable = this.coupons[this.currentSelected].threshold
			} else {
				balanceTmp.c_id = 2
				balanceTmp.amount_payable = this.coupons[1].threshold
			}

			this.balanceTmp = balanceTmp

			uni.navigateTo({
				url: '../balanceDetail/balanceDetail?balanceTmp=' + JSON.stringify(this.balanceTmp) + '&targetUrl=' + this.targetUrl

			})
		}
	}
}

